import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

/**
* Navigation component
*
* The Navigation component takes an array of your Ghost
* navigation property that is fetched from the settings.
* It differentiates between absolute (external) and relative link (internal).
* You can pass it a custom class for your own styles, but it will always fallback
* to a `site-nav-item` class.
*
*/
const Navigation = ({ post, linkTags }) => {
    const items = [];
    if (post && post.tags) {
        for(let i = 0; i < post.tags.length; i++) {
            if (post.tags[i].slug !== 'homepage') {
                if(linkTags)
                    items.push(<span key={i}>{(i !== 0) ? ', ' : ''}<Link to={`/tag/${post.tags[i].slug}`}>{post.tags[i].name}</Link ></span>)
                else 
                    items.push(<span key={i}>{(i !== 0) ? ', ' : ''}{post.tags[i].name}</span>)
            }
        }
    }
    return (<span className="tags-container">{items}</span>);
}

Navigation.defaultProps = {
    post: {},
    linkTags: false,
}


export default Navigation
