import React, { useState } from 'react'
import styled from '@emotion/styled'
import { css } from 'emotion'
import { Link, StaticQuery, graphql } from 'gatsby'
import NavbarNavigation from './NavbarNavigation.js'

function Navbar (props) {
    const site = props.site;

    const [active, setActive] = useState(false);

    return <div className={css`
        background-color: var(--color-bg);
    `} data-active={active}>
        {/* The main header section on top of the screen */}
        <header className={css`
            display: flex;
            justify-content: space-between;
            padding: 1rem 2rem;
            max-width: var(--max-container-width);
            margin: 0 auto;
            --link-color: #53696a;
            @media (max-width: 800px) {
                padding: 1rem;
            }
        `}>
            <Link to="/">
                <img className={css`
                    max-width: 100%;
                    margin: -1rem;
                    padding: 1rem;
                    box-sizing: content-box;
                    @media (max-width: 800px) {
                        width: 35px;
                        height: auto;
                    }
                `} width="97" height="108" src={site.logo} alt={site.title} />
            </Link>
            
            <div className={css`
                padding: 1rem;
                margin: -1rem;
                cursor: pointer;
                
                @media (min-width: 799px) {
                    display: none;
                }
            `} onClick={()=>{
                setActive(!active);
            }}>
                <img src={'/images/icons/hamburger.svg'} />
            </div>

            <div className={css`
                text-align: right;
                @media screen and (max-width: 800px) {
                    position: fixed;
                    z-index: 9999;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    width: 100%;
                    height: 100%;
                    ${active ? `background: rgba(0,0,0,0.5);` : `background: rgba(0,0,0,0);`}
                    transition-duration: .4s;
                    transition-timing-function: cubic-bezier(.25,.5,.5,1);

                    pointer-events: none;

                    & > div {
                        display: flex;
                        flex-direction: column;
                        position: fixed;
                        top 0;
                        right: 0%;
                        ${active ? `transform: translate(0%, 0%);` : `transform: translate(100%, 0%);`}
                        transition-duration: .4s;
                        transition-timing-function: cubic-bezier(.25,.5,.5,1);
                        bottom: 0;
                        background-color: var(--color-bg);
                        padding: 1rem;
                        width: 10rem;
                        box-sizing: content-box;
                        color: var(--color-base);
                    }
                }
            `}>
                <div className={css`
                    pointer-events: auto;
                `}>
                    <div className={css`
                        padding: 0 0 1rem 0;
                        width: 100%;
                        flex-flow: row-reverse nowrap;

                        @media (min-width: 799px) {
                            display: none;
                        }
                    `} onClick={()=>{
                        setActive(!active);
                    }}>
                        <img src={'/images/icons/close.svg'} />
                    </div>
                    <NavbarNavigation data={site.navigation} />
                </div>
            </div>
        </header>
    </div>
}

export default Navbar;